import React, { useState, useEffect } from "react";
import "../Styles/Profile.css";

const ProfilePage = ({ user, setUser })=> {
  const [fullName, setFullName] = useState("");
  const [username, setUsername] = useState("");
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [currentEmail, setCurrentEmail] = useState("");
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");

  useEffect(() => {
    async function fetchProfile() {
      try {
        const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/profile`, { credentials: "include" });
        const data = await res.json();
        if (data.success) {
          setFullName(data.user.full_name || "");
          setUsername(data.user.username || "");
          setCurrentEmail(data.user.email || "");
        }
      } catch (err) {
        console.error(err);
      }
    }
    fetchProfile();
  }, []);

  const handleUpdateProfile = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage("");

    try {
      const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/profile`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({ full_name: fullName, username }),
      });
      const data = await res.json();
      if (data.success) {
        setMessage("Profile updated successfully!");
      } else {
        setMessage(data.message);
      }
    } catch (err) {
      setMessage("Failed to update profile."+err);
    } finally {
      setLoading(false);
    }
  };

  const handleChangePassword = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage("");

    try {
      const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/profile/password`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({ currentPassword, newPassword }),
      });
      const data = await res.json();
      setMessage(data.message);
    } catch (err) {
      setMessage("Failed to change password.");
    } finally {
      setLoading(false);
      setCurrentPassword("");
      setNewPassword("");
    }
  };

  const handleEmailChange = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage("");

    try {
      const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/profile/email`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({ currentEmail, setCurrentEmail }),
      });
      const data = await res.json();
      setMessage(data.message);
    } catch (err) {
      setMessage("Failed to change email.",err);
    } finally {
      setLoading(false);
      setCurrentPassword("");
      setNewPassword("");
    }
  };

  return (
    <div className="profile-container">
      <h2 className="profile-title">My Profile</h2>
      {message && <p className="profile-message">{message}</p>}

      <div className="profile-card">
        <form className="profile-form" onSubmit={handleUpdateProfile}>
          <h3>Update Info</h3>
          <label>Full Name</label>
          <input
            type="text"
            value={fullName}
            onChange={(e) => setFullName(e.target.value)}
            placeholder="Enter your full name"
          />

          <label>Username</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            placeholder="Choose a username"
          />

          <button type="submit" className="btn-primary" disabled={loading}>
            {loading ? "Saving..." : "Update Profile"}
          </button>
        </form>

        <form className="profile-form" onSubmit={handleChangePassword}>
          <h3>Change Password</h3>
          <label>Current Password</label>
          <input
            type="password"
            value={currentPassword}
            onChange={(e) => setCurrentPassword(e.target.value)}
            placeholder="Enter current password"
          />

          <label>New Password</label>
          <input
            type="password"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            placeholder="Enter new password"
          />

          <button type="submit" className="btn-primary" disabled={loading}>
            {loading ? "Saving..." : "Change Password"}
          </button>
        </form>

        <form className="profile-form" onSubmit={handleEmailChange}>
          <h3>Change Email</h3>
          <label>Current Email</label>
          <input
            type="email"
            value={currentEmail}
            onChange={(e) => setCurrentEmail(e.target.value)}
            placeholder="Enter current Email"
          />


          <button type="submit" className="btn-primary" disabled={loading}>
            {loading ? "Saving..." : "Change Email"}
          </button>
        </form>
      </div>
      <div className="container-dashboard"></div>
    </div>
  );
}
export default ProfilePage;