import React, { useEffect, useState } from "react";
import axios from "axios";
import "../Styles/AdminCarousel.css";
import { useNavigate } from "react-router-dom";

const AdminCarousel = () => {
    const navigate = useNavigate();
  const [slides, setSlides] = useState([]);
  const [loading, setLoading] = useState(true);
  const [editingSlide, setEditingSlide] = useState(null); 
  const [formData, setFormData] = useState({id:"", text: "", image: "" });
  useEffect(() => {
    const fetchSlides = async () => {
      try {
        const res = await axios.get("http://localhost:8000/api/carousel");
        console.log("Fetched carousel data:", res.data);
        setSlides(res.data.slides || []);
        
      } catch (err) {
        console.error("Failed to load carousel:", err);
      } finally {
        setLoading(false);
      }
    };
    fetchSlides();
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleEdit = (slide) => {
    setEditingSlide(slide.id);
    setFormData({ text: slide.text, image: slide.image });
  };

  const handleSave = async () => {
    try {
      if (editingSlide) {
        await axios.put(`${process.env.REACT_APP_BACKEND_URL}/api/carousel/${editingSlide}`, formData);
        setSlides(slides.map(s => (s.id === editingSlide ? { ...s, ...formData } : s)));
      } else {
        const res = await axios.post(`${process.env.REACT_APP_BACKEND_URL}/api/carousel`, formData);
        setSlides([...slides, res.data.slide]);
      }
      setEditingSlide(null);
      setFormData({ text: "", image: "" });
    } catch (err) {
      console.error("Failed to save slide:", err);
      alert("Failed to save slide.");
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm("Are you sure you want to delete this slide?")) return;
    try {
      await axios.delete(`${process.env.REACT_APP_BACKEND_URL}/api/carousel/${id}`);
      setSlides(slides.filter(s => s.id !== id));
    } catch (err) {
      console.error("Failed to delete slide:", err);
    }
  };

  if (loading) return <div className="admin-carousel-container">Loading...</div>;

  return (
    <div className="bg-background">
    <div className="admin-carousel-container">
      <h1>Manage Carousel Slides</h1>

      <div className="carousel-form">
        <label>
          Slide Text:
          <input
            type="text"
            name="text"
            value={formData.text}
            onChange={handleChange}
          />
        </label>

        <label>
          Image URL:
          <input
            type="text"
            name="image"
            value={formData.image}
            onChange={handleChange}
          />
        </label>

        <button className="save-btn" onClick={handleSave}>
          {editingSlide ? "Update Slide" : "Add Slide"}
        </button>
      </div>

      <div className="carousel-list">
        {slides.map((slide) => (
          <div key={slide.id} className="carousel-item">
            <img src={`${process.env.REACT_APP_BACKEND_URL}${slide.image}`} alt={slide.text} />
            <p>{slide.text}</p>
            <div className="item-actions">
              <button className="edit-btn" onClick={() => handleEdit(slide)}>Edit</button>
              <button className="delete-btn" onClick={() => handleDelete(slide.id)}>Delete</button>
            </div>
          </div>
        ))}
      </div>
      <button className="back-btn" onClick={() => navigate("/admin")}>
        Back to Admin Panel
      </button>
      
    </div>
    </div>
  );
};

export default AdminCarousel;
