
import React, { useEffect, useState } from "react";
import "../Styles/Contact.css";
import Arrows from "../Components/Arrows";


const ContactPage =()=> {
    const [formSent, setFormSent] = useState(false);
    const [contact, setContact] = useState(null);
    const [formData, setFormData] = useState({
        name: "",
        email: "",
        message: ""
    });

    useEffect(() => {
        fetch(`${process.env.REACT_APP_BACKEND_URL}/api/contact`)
        .then((res) => res.json())
        .then(setContact)
        .catch((err) => console.error("Error fetching contact page data:", err));
    }, []);



    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/contact/send`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(formData)
            });

            const data = await response.json();

            if (data.success) {
                setFormData({ name: "", email: "", message: "" });
                setFormSent(true);
            } else {
                alert(data.message || "Failed to send message.");
            }
        } catch (err) {
            console.error(err);
            alert("Something went wrong. Try again later.");
        }
    };

    if (!contact) return <div className="loading">Loading...</div>;

    return (
        <div className="contact-page">
            <section className="contact-hero">
                <div className="contact-hero-content">
                    <h1>Contact Us</h1>
                    <p>Have questions or feedback? Reach out to our team and well get back to you promptly.</p>
                </div>
                {Arrows()}
            </section>


            <section className="contact-form-section">
                <div className="container">
                    <form className="contact-form" onSubmit={handleSubmit}>
                        <div className="form-group">
                            <label>Name</label>
                            <input
                                type="text"
                                name="name"
                                value={formData.name}
                                onChange={handleChange}
                                required
                                placeholder="Your Name"
                            />
                        </div>
                        <div className="form-group">
                            <label>Email</label>
                            <input
                                type="email"
                                name="email"
                                value={formData.email}
                                onChange={handleChange}
                                required
                                placeholder="you@example.com"
                            />
                        </div>
                        <div className="form-group">
                            <label>Message</label>
                            <textarea
                                name="message"
                                value={formData.message}
                                onChange={handleChange}
                                rows="5"
                                required
                                placeholder="Your message..."
                            ></textarea>
                        </div>
                        <button
                         type="submit"
                         //className={'btn-contact ${formSent ? "btn-success" : "btn-primary" }'} feature not working
                         className={formSent ? "btn-success" : "btn-contact" }

                         disabled={formSent}
                         >{formSent ?"Message was successfully sent!" : "Send Message"}</button>
                    </form>

                    <div className="contact-info">
                        <h3>{contact.title}</h3>
                        <p>Email: {contact.email}</p>
                        <p>Phone: {contact.phone}</p>
                        <p>Address: {contact.address}</p>
                    </div>
                </div>
            </section>
            <div className="container-grid"></div>
        </div>
    );
}
export default ContactPage;


