import { useEffect, useState } from "react";
import "../Styles/AdminStats.css";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const AdminStats = () => {
  const navigate = useNavigate();
  const [stats, setStats] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [formData, setFormData] = useState({ label: "", value: "" });
  const [editingId, setEditingId] = useState(null);


  useEffect(() => {
    const fetchStats = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/getstats`);
        setStats(res.data.rows || []);
      } catch (err) {
        console.error(err);
        setError("Failed to load stats.");
      } finally {
        setLoading(false);
      }
    };
    fetchStats();
  }, []);


  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };


  const handleAdd = async () => {
    if (!formData.label || !formData.value) return alert("Fill all fields");
    try {
      const res = await axios.post(`${process.env.REACT_APP_BACKEND_URL}/api/getstats`, formData);
      if (res.data.success) {
        setStats([ { id: Date.now(), ...formData }, ...stats ]); 
        setFormData({ label: "", value: "" });
      } else alert(res.data.message);
    } catch (err) {
      console.error(err);
      alert("Failed to add stat");
    }
  };


  const handleEdit = (stat) => {
    setEditingId(stat.id);
    setFormData({ label: stat.label, value: stat.value });
  };


  const handleSave = async () => {
    if (!formData.label || !formData.value) return alert("Fill all fields");
    try {
      const res = await axios.put(`http://localhost:8000/api/getstats/${editingId}`, formData);
      if (res.data.success) {
        setStats(stats.map((s) => (s.id === editingId ? { ...s, ...formData } : s)));
        setEditingId(null);
        setFormData({ label: "", value: "" });
      } else alert(res.data.message);
    } catch (err) {
      console.error(err);
      alert("Failed to update stat");
    }
  };


  const handleDelete = async (id) => {
    try {
      const res = await axios.delete(`http://localhost:8000/api/getstats/${id}`);
      if (res.data.success) {
        setStats(stats.filter((s) => s.id !== id));
      } else alert(res.data.message);
    } catch (err) {
      console.error(err);
      alert("Failed to delete stat");
    }
  };

  if (loading) return <div className="admin-stats-container">Loading...</div>;
  if (error) return <div className="admin-stats-container error">{error}</div>;

  return (
    <div className="admin-stats-container">
      <h1>Manage Stats</h1>

      <div className="stats-form">
        <label>
          Label:
          <input
            type="text"
            name="label"
            value={formData.label}
            onChange={handleChange}
          />
        </label>

        <label>
          Value:
          <input
            type="text"
            name="value"
            value={formData.value}
            onChange={handleChange}
          />
        </label>

        <div className="actions">
          {editingId ? (
            <>
              <button className="save-btn" onClick={handleSave}>
                Save
              </button>
              <button
                className="cancel-btn"
                onClick={() => {
                  setEditingId(null);
                  setFormData({ label: "", value: "" });
                }}
              >
                Cancel
              </button>
            </>
          ) : (
            <button className="add-btn" onClick={handleAdd}>
              Add Stat
            </button>
          )}
        </div>
      </div>

      <div className="stats-list">
        {stats.map((stat) => (
          <div key={stat.id} className="stat-card">
            <div>
              <h3>{stat.value}</h3>
              <p>{stat.label}</p>
            </div>
            <div className="card-actions">
              <button className="edit-btn" onClick={() => handleEdit(stat)}>
                Edit
              </button>
              <button className="delete-btn" onClick={() => handleDelete(stat.id)}>
                Delete
              </button>
            </div>
          </div>
        ))}
      </div>
      <button className="back-btn" onClick={() => navigate("/admin")}>Back to Admin Panel
      </button>
    </div>
  );
};

export default AdminStats;
