import React, { useEffect, useState } from "react";
import "../Styles/AdminFeatures.css";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const AdminFeatures = () => {
  const navigate = useNavigate();
  const [features, setFeatures] = useState([]);
  const [loading, setLoading] = useState(true);
  const [editingFeature, setEditingFeature] = useState(null);
  const [newFeature, setNewFeature] = useState({ title: "", description: "" ,icon : ""});
  useEffect(() => {
    const fetchFeatures = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/features`);
        console.log("Fetched features:", res.data.rows);
        setFeatures(res.data.rows);
      } catch (err) {
        console.error("Failed to load features:", err);
      } finally {
        setLoading(false);
      }
    };
    fetchFeatures();
  }, []);


  const handleEditChange = (e) => {
    setEditingFeature({
      ...editingFeature,
      [e.target.name]: e.target.value,
    });
  };


  const saveEdit = async () => {
    try {
      const res = await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/features/${editingFeature.id}`,
        editingFeature
      );

      if (res.data.success) {
        setFeatures((prev) =>
          prev.map((f) => (f.id === editingFeature.id ? editingFeature : f))
        );
        setEditingFeature(null);
        alert("Feature updated successfully!");
      }
    } catch (err) {
      console.error(err);
      alert("Error updating feature.");
    }
  };

  const deleteFeature = async (id) => {
    if (!window.confirm("Are you sure you want to delete this feature?")) return;

    try {
      const res = await axios.delete(`${process.env.REACT_APP_BACKEND_URL}/api/features/${id}`);
      if (res.data.success) {
        setFeatures((prev) => prev.filter((f) => f.id !== id));
      }
    } catch (err) {
      console.error(err);
      alert("Error deleting feature.");
    }
  };

  const addFeature = async () => {
    if (!newFeature.title.trim() || !newFeature.description.trim()) {
      return alert("Title & Description cannot be empty.");
    }

    try {
      const res = await axios.post(`${process.env.REACT_APP_BACKEND_URL}/api/features`, newFeature);

      if (res.data.success) {
        setFeatures([...features, res.data]);
        setNewFeature({ title: "", description: "" });
        alert("Feature added successfully!");
      }
    } catch (err) {
      console.error(err);
      alert("Error adding feature.");
    }
  };

  if (loading) return <div className="admin-features-container">Loading...</div>;

  return (
    <div className="admin-features-container">
      <h1>Edit Features</h1>

      <div className="features-list">
        {features.map((feature) => (
          <div key={feature.id} className="feature-card">
            {editingFeature?.id === feature.id ? (
              <>
              <input
                  type="text"
                  name="icon"
                  value={editingFeature.icon}
                  onChange={handleEditChange}
                />
                <input
                  type="text"
                  name="title"
                  value={editingFeature.title}
                  onChange={handleEditChange}
                />
                <textarea
                  name="description"
                  value={editingFeature.description}
                  onChange={handleEditChange}
                />
                <button className="save-btn" onClick={saveEdit}>Save</button>
                <button className="cancel-btn" onClick={() => setEditingFeature(null)}>
                  Cancel
                </button>
              </>
            ) : (
              <>
                <h3>{feature.title}</h3>
                <p>{feature.description}</p>

                <div className="actions">
                  <button
                    className="edit-btn"
                    onClick={() => setEditingFeature(feature)}
                  >
                    Edit
                  </button>
                  <button
                    className="delete-btn"
                    onClick={() => deleteFeature(feature.id)}
                  >
                    Delete
                  </button>
                </div>
              </>
            )}
          </div>
        ))}
      </div>

      <h2>Add New Feature</h2>
      <div className="add-feature-form">
        <input
          type="text"
          placeholder="Feature Icon"
          value={newFeature.icon}
          onChange={(e) =>
            setNewFeature({ ...newFeature, icon: e.target.value })
          }
        />
        <input
          type="text"
          placeholder="Feature title"
          value={newFeature.title}
          onChange={(e) =>
            setNewFeature({ ...newFeature, title: e.target.value })
          }
        />
        <textarea
          placeholder="Feature description"
          value={newFeature.description}
          onChange={(e) =>
            setNewFeature({ ...newFeature, desc: e.target.value })
          }
        />
        <button className="add-btn" onClick={addFeature}>
          Add Feature
        </button>
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>
        Back to Admin Panel
      </button>

      <div className="admin-edit-decoration"></div>
    </div>
  );
};

export default AdminFeatures;
