import React, { useEffect, useState } from "react";
import "../Styles/AdminUsers.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const AdminUsers = () => {
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const [filteredUsers, setFilteredUsers] = useState([]);
  const [editingUser, setEditingUser] = useState(null);
  const [newUsername, setNewUsername] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [newEmail, setNewEmail] = useState("");
  const [FullName, setFullName] = useState("");
  const [newRole, setNewRole] = useState('');
  const [searchQuery, setSearchQuery] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/users`, {
          withCredentials: true,
        });
        setUsers(res.data);
        setFilteredUsers(res.data);
      } catch (err) {
        console.error(err);
        setError("Failed to load users.");
      } finally {
        setLoading(false);
      }
    };
    fetchUsers();
  }, []);

  useEffect(() => {
    const query = searchQuery.toLowerCase();
    setFilteredUsers(
      users.filter(
        (u) =>
          u.username.toLowerCase().includes(query) ||
          u.full_name.toLowerCase().includes(query) ||
          u.email.toLowerCase().includes(query) ||
          u.role.toLowerCase().includes(query)
      )
    );
  }, [searchQuery, users]);

  const handleEdit = (user) => {
    setEditingUser(user);
    setNewUsername(user.username);
    setNewEmail(user.email);
    setFullName(user.full_name);
    setNewRole(user.role);
    setNewPassword("");
  };


  const handleSave = async () => {
    try {
      await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${editingUser.user_id}`,
        {
          full_name: FullName,
          email: newEmail,
          username: newUsername,
          ...(newPassword && { password: newPassword }),
          role: newRole,
        },
        { withCredentials: true }
      );

      setUsers((prev) =>
        prev.map((u) =>
          u.user_id === editingUser.user_id
            ? {
              ...u, full_name: FullName,
              email: newEmail,
              username: newUsername,
              role: newRole
            }
            : u
        )
      );
      setEditingUser(null);
    } catch (err) {
      console.error(err);
      alert("Failed to update user.");
    }
  };


  const handleBanToggle = async (user_id) => {
    if (!window.confirm("Are you sure you want to toggle ban for this user?")) return;
    try {
      await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/users/${user_id}/ban`,
        {},
        { withCredentials: true }
      );
      setUsers((prev) =>
        prev.map((u) =>
          u.user_id === user_id
            ? { ...u, status: u.status === "banned" ? "active" : "banned" }
            : u
        )
      );
    } catch (err) {
      console.error(err);
      alert("Failed to toggle ban.");
    }
  };

  if (loading) return <div className="admin-users-container">Loading users...</div>;
  if (error) return <div className="admin-users-container error">{error}</div>;

  return (
    <div className="admin-users-container">
      <h1>Manage Users</h1>
      <p className="admin-description">Add, edit, or ban user accounts.</p>
      <input
        type="text"
        className="search-input"
        placeholder="Search by name, username, email, or role..."
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
      />

      <div className="table-wrapper">
        <table className="admin-users-table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Full Name</th>
              <th>Username</th>
              <th>Email</th>
              <th>Role</th>
              <th>Status</th>
              <th>Created</th>
              <th>Actions</th>
            </tr>
          </thead>

          <tbody>
            {filteredUsers.map((user) => (
              <tr key={user.user_id} className={user.status === "banned" ? "banned" : ""}>
                <td>{user.user_id}</td>
                <td>{user.full_name}</td>
                <td>{user.username}</td>
                <td>{user.email}</td>
                <td>{user.role}</td>

                <td>
                  <span className={`status-badge ${user.status === "banned" ? "banned" : "active"}`}>
                    {user.status || "Unknown"}
                  </span>
                </td>
                <td>{new Date(user.created_at).toLocaleDateString()}</td>
                <td>
                  <button className="admin-btn edit" onClick={() => handleEdit(user)}>Edit</button>
                  <button
                    className={`admin-btn ban ${user.status === "banned" ? "unban" : ""}`}
                    onClick={() => handleBanToggle(user.user_id)}
                  >
                    {user.status === "banned" ? "Unban" : "Ban"}
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {editingUser && (
        <div className="edit-modal">
          <div className="modal-content">
            <h3>Edit User #{editingUser.user_id}</h3>
            <input
              type="text"
              value={FullName}
              onChange={(e) => setFullName(e.target.value)}
              placeholder="Full Name"
            />
            <input
              type="text"
              value={newUsername}
              onChange={(e) => setNewUsername(e.target.value)}
              placeholder="New username"
            />
            <input
              type="email"
              value={newEmail}
              onChange={(e) => setNewEmail(e.target.value)}
              placeholder="New Email"
            />
            <input
              type="password"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              placeholder="New password (optional)"
            />
            <label htmlFor="user-role">Role:</label>
            <select
              id="user-role"
              value={newRole}
              onChange={(e) => setNewRole(e.target.value)}
            >
              <option value="user">User</option>
              <option value="admin">Admin</option>
            </select>
            <div className="edit-actions">
              <button className="save" onClick={handleSave}>Save</button>
              <button className="cancel" onClick={() => setEditingUser(null)}>Cancel</button>
            </div>
          </div>
        </div>
      )}

      <button className="back-btn" onClick={() => navigate("/admin")}>Back to Admin Panel
      </button>
      <div className="admin-edit-decoration"></div>
    </div>
  );
}
export default AdminUsers;