import React, { useEffect, useState } from "react";
import "../Styles/AdminSupport.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const AdminSupport = () => {
  const navigate = useNavigate();
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [notification, setNotification] = useState(null); 
  const showNotification = (message, type = 'success') => {
    setNotification({ message, type });
    setTimeout(() => {
      setNotification(null);
    }, 3000); 
  };
  const fetchMessages = async () => {
    try {
      setLoading(true);
      const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/contact/getmessages`, {
        withCredentials: true,
      });

      if (res.data) {
        console.log("Fetched messages:", res.data);
        setMessages(res.data);
      }
    } catch (err) {
      console.error("Failed to fetch messages:", err);
      setError("Failed to load contact messages. Check the backend API.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchMessages();
  }, []);

  // --- DELETE FUNCTION ---
  const handleDelete = async (messageId) => {
    if (!window.confirm(`Are you sure you want to delete message ID ${messageId}?`)) {
      return;
    }

    try {
      // API Call (DELETE request)
      await axios.delete(
        `${process.env.REACT_APP_BACKEND_URL}/api/contact/messages/${messageId}`,
        { withCredentials: true }
      );

      // Optimistic Update: Remove the message from local state
      setMessages((prevMessages) =>
        prevMessages.filter((msg) => msg.id !== messageId)
      );

      showNotification(`Message ID ${messageId} deleted successfully!`, 'success');
    } catch (err) {
      console.error(`Failed to delete message ID ${messageId}:`, err);
      showNotification("Failed to delete the message. Check your permissions and API.", 'error');
    }
  };

  // --- MARK AS READ/UNREAD FUNCTION (UI logic only) ---
  const toggleReadStatus = (messageId) => {
    // NOTE: This function requires a backend PUT/PATCH endpoint to persist the change.
    setMessages((prevMessages) =>
      prevMessages.map((msg) =>
        msg.id === messageId ? { ...msg, is_read: !msg.is_read } : msg
      )
    );
    // Optionally call backend here:
    // axios.patch(`http://localhost:8000/api/contact/messages/read/${messageId}`, { is_read: !message.is_read })
  };
  // --------------------------------------------------

  if (loading) return <div className="admin-support-container">Loading messages...</div>;
  if (error) return <div className="admin-support-container error">{error}</div>;

  return (
    <div className="bg-background">
    <div className="admin-support-container">
      {/* Notification Banner */}
      {notification && (
        <div className={`notification ${notification.type}`}>
          {notification.message}
        </div>
      )}

      <h1>Customer Support Messages ({messages.length})</h1>

      <div className="messages-list">
        {messages.length === 0 ? (
          <p className="no-messages">No contact messages found.</p>
        ) : (
          messages.map((msg) => (
            // Use msg.is_read to conditionally style the card
            <div 
                key={msg.id} 
                className={`message-card ${msg.is_read ? 'read' : 'unread'}`}
            >
              <div className="message-header">
                <span className="sender-name">
                  <i className="fas fa-user"></i>
                  **Name:** {msg.name}
                </span>
                <span className="sender-email">
                  <i className="fas fa-envelope"></i>
                  **Email:** {msg.email}
                </span>
              </div>

              <p className="message-body">
                **Message:** {msg.message}
              </p>

              <div className="message-footer">
                <span className="timestamp">
                    **Received:** {new Date(msg.created_at).toLocaleString()}
                </span>
                
                <div className="message-actions">
                  {/* Toggle Read Status Button */}
                  <button 
                      className={`read-btn ${msg.is_read ? 'mark-unread' : 'mark-read'}`}
                      onClick={() => toggleReadStatus(msg.id)}
                  >
                      {msg.is_read ? 'Mark Unread' : 'Mark Read'}
                  </button>
                  <button
                    className="delete-btn"
                    onClick={() => handleDelete(msg.id)}
                  >
                    Delete
                  </button>
                </div>
              </div>
            </div>
          ))
        )}
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>
        Back to Admin Panel
      </button>
    </div>
    </div>
  );
};

export default AdminSupport;