import React, { useState, useEffect } from "react";
import "../Styles/Carousel.css";

export default function Carousel() {
  const [slides, setSlides] = useState([]);
  const [current, setCurrent] = useState(0);

  useEffect(() => {
    const fetchSlides = async () => {
      try {
        const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/carousel`);
        const data = await res.json();
        setSlides(data.slides);
      } catch (err) {
        console.error("Failed to load carousel:", err);
      }
    };

    fetchSlides();
  }, []);

  useEffect(() => {
    if (slides.length === 0) return;

    const interval = setInterval(() => {
      setCurrent((prev) => (prev + 1) % slides.length);
    }, 5000);

    return () => clearInterval(interval);
  }, [slides]);

  if (slides.length === 0) {
    return <div className="carousel-loading">Loading...</div>;
  }

  const goToSlide = (index) => setCurrent(index);
  const prevSlide = () =>
    setCurrent((prev) => (prev === 0 ? slides.length - 1 : prev - 1));
  const nextSlide = () => setCurrent((prev) => (prev + 1) % slides.length);

  return (
    <section className="carousel-section">
      <div className="carousel-container">
        
        {slides.map((slide, index) => (
          <div
            key={slide.id}
            className={`carousel-slide ${index === current ? "active" : "inactive"}`}
          >
            <img
              src={`http://localhost:8000${slide.image}`}
              alt={slide.text}
              className="carousel-img"
            />
            <div className="carousel-caption">
              <p>{slide.text}</p>
            </div>
          </div>
        ))}

        <button className="carousel-btn prev" onClick={prevSlide}></button>
        <button className="carousel-btn next" onClick={nextSlide}></button>

        <div className="carousel-indicators">
          {slides.map((_, i) => (
            <span
              key={i}
              className={`indicator ${i === current ? "active" : ""}`}
              onClick={() => goToSlide(i)}
            ></span>
          ))}
        </div>

      </div>
    </section>
  );
}
