import React, { useEffect, useState } from "react";
import axios from "axios";
import "../Styles/About.css";
import Arrows from "../Components/Arrows";
import TeamInfo from "../Components/TeamInfo";


const AboutPage = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    const fetchAbout = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/about`, {
          withCredentials: true,
        });
        console.log(res.data);
        setData(res.data);
      } catch (err) {
        console.error(err);
      }
    };

    fetchAbout();
  }, []);

  if (!data) return <div className="loading">Loading...</div>;


  return (
    <div className="about-page">
      <section className="about-hero">
        <div className="about-hero-content">
          <h1>{data.hero_title}</h1>
          <p>{data.hero_description}</p>
        </div>
        {Arrows()}
        <div className="container-grid"></div>
      </section>

      <section className="about-mission">
        <div className="container">
          <h2>{data.mission_title}</h2>
          <p>{data.mission_text}</p>
        </div>
      </section>
      <TeamInfo />
      <section className="about-cta">
        <h2>{data.cta_title}</h2>
        <p>{data.cta_text}</p>
        <a href={data.cta_link} className="btn-primary">Get Started</a>
      </section>
    </div>
  );
};
export default AboutPage;