import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "../Styles/AdminHero.css";

const AdminHero = () => {
  const navigate = useNavigate();
  const [hero, setHero] = useState({ title: "", subtitle: "", ctaPrimary: "", ctaSecondary:"" });
  const [formData, setFormData] = useState({title: "", subtitle: "", ctaPrimary: "", ctaSecondary:""});
  const [editing, setEditing] = useState(false);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const fetchHero = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/hero`);
        console.log("Fetched hero data:", res.data);
        setHero(res.data[0] || {});
        setFormData(res.data[0] || {});
      } catch (err) {
        console.error("Failed to fetch hero data:", err);
      } finally {
        setLoading(false);
        console.log(formData[0]);
      }
    };

    fetchHero();
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSave = async () => {
    try {
      const response = await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/hero`,
        formData
      );
      if (response.data.success || response.data.message) {
        setHero(formData);
        setEditing(false);
        alert(" Hero section updated successfully!");
      }
    } catch (err) {
      console.error(err);
      alert(" Failed to update hero section");
    }
  };

  if (loading) return <div className="admin-hero-container">Loading...</div>;

  return (
    <div className="admin-hero-container">
      <h1>Edit Hero Section</h1>

      <div className="hero-form">
        <label>
          Title:
          <input
            type="text"
            name="title"
            value={formData.title || ""}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          Subtitle:
          <input
            type="text"
            name="subtitle"
            value={formData.subtitle || ""}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          ctaPrimary:
          <input
            type="text"
            name="ctaPrimary"
            value={formData.ctaPrimary || ""}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>
        <label>
          ctaSecondary:
          <input
            type="text"
            name="ctaSecondary"
            value={formData.ctaSecondary || ""}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <div className="actions">
          {editing ? (
            <>
              <button className="save-btn" onClick={handleSave}>Save</button>
              <button className="cancel-btn" onClick={() => setEditing(false)}>Cancel</button>
            </>
          ) : (
            <button className="edit-btn" onClick={() => setEditing(true)}>Edit</button>
          )}
        </div>
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>
        Back to Admin Panel
      </button>
    </div>
  );
};

export default AdminHero;
