import React, { useEffect, useState } from "react";
import "../Styles/AdminContact.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const  AdminContact =()=> {
  const navigate = useNavigate();
  const [contact, setContact] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [editing, setEditing] = useState(false);
  const [formData, setFormData] = useState({
    id: null,
    title: "",
    email: "",
    phone: "",
    address: "",
  });

  useEffect(() => {
}, [contact]);

  useEffect(() => {
    const fetchContact = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/contact`, {
          withCredentials: true,
        });
        if (res.data) {
          setContact(res.data);
          setFormData({
            id: res.data.id || null,
            title: res.data.title || "",
            email: res.data.email || "",
            phone: res.data.phone || "",
            address: res.data.address || "",
          });
        }
      } catch (err) {
        console.error(err);
        setError("Failed to load contact info.");
      } finally {
        setLoading(false);
      }
    };
    fetchContact();
  }, []);
  const handleChange = (e) => {
    setFormData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleSave = async () => {
  if (!contact?.id) {
    alert("Contact ID is missing. Cannot save changes.");
    return;
  }

  try {
    await axios.put(
      `${process.env.REACT_APP_BACKEND_URL}/api/contact/${contact.id}`,
      formData,
      { withCredentials: true }
    );
    setContact({ ...contact, ...formData });
    setEditing(false);
    alert("Contact info updated successfully!");
  } catch (err) {
    console.error(err);
    alert("Failed to update contact info.");
  }
};


  if (loading) return <div className="admin-contact-container">Loading...</div>;
  if (error) return <div className="admin-contact-container error">{error}</div>;

  return (
    <div className="admin-contact-container">
      <h1>Edit Contact Information</h1>

      <div className="contact-card">
        <label>
          Title:
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          Email:
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          Phone:
          <input
            type="text"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          Address:
          <input
            type="text"
            name="address"
            value={formData.address}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <div className="actions">
          {editing ? (
            <>
              <button className="save-btn" onClick={handleSave}>Save</button>
              <button className="cancel-btn" onClick={() => setEditing(false)}>Cancel</button>
            </>
          ) : (
            <button className="edit-btn" onClick={() => setEditing(true)}>Edit</button>
          )}
        </div>
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>Back to Admin Panel
      </button>
      <div className="admin-edit-decoration"></div>
    </div>
  );
}
export default AdminContact;