import React, { useState, useMemo, useEffect } from "react";
import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip } from "recharts";
import { useNavigate } from "react-router-dom";
import "../Styles/Dashboard.css";

const  Dashboard = ()=> {
  const navigate = useNavigate();
  const [userId, setUserId] = useState(null);
  const [transactions, setTransactions] = useState([]);
  const [budget, setBudget] = useState(0);
  const [filterText, setFilterText] = useState("");
  const [form, setForm] = useState({ date: "", desc: "", amount: "", category: "" });
  const [loading, setLoading] = useState(true);
  const monthYear = new Date().toISOString().slice(0, 7);
  const COLORS = ["#0d6efd", "#198754", "#ffc107", "#dc3545", "#6f42c1"];

  function currency(n) {
    const sign = n < 0 ? "-" : "";
    return `${sign}$${Math.abs(n).toFixed(2)}`;
  }

  useEffect(() => {
    async function fetchUser() {
      try {
        const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/auth/me`, { credentials: "include" });
        const data = await res.json();
        if (data.success && data.user) setUserId(data.user.id);
        else navigate("/");
      } catch {
        navigate("/");
      }
    }
    fetchUser();
  }, [navigate]);

  useEffect(() => {
    if (!userId) return;
    async function fetchData() {
      try {
        const [txRes, budgetRes] = await Promise.all([
          fetch(`${process.env.REACT_APP_BACKEND_URL}/api/transactions/${userId}`),
          fetch(`${process.env.REACT_APP_BACKEND_URL}/api/budget/${userId}/${monthYear}`)
        ]);
        const txData = await txRes.json();
        const budgetData = await budgetRes.json();
        if (txData.success) setTransactions(txData.transactions);
        if (budgetData.success && budgetData.budget) setBudget(budgetData.budget.amount);
      } catch (err) {
        alert("Failed to load data: " + err.message);
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, [userId]);

  const totals = useMemo(() => {
    let spent = 0;
    let income = 0;
    for (const t of transactions) {
      if (t.amount > 0) spent += t.amount;
      else income += -t.amount;
    }
    return { spent, income };
  }, [transactions]);

  const categoryBreakdown = useMemo(() => {
    const map = {};
    for (const t of transactions) {
      const c = t.category || "Other";
      map[c] = (map[c] || 0) + (t.amount > 0 ? t.amount : 0);
    }
    return Object.entries(map).map(([name, value]) => ({ name, value }));
  }, [transactions]);

  async function addTransaction(e) {
    e.preventDefault();
    const amt = parseFloat(form.amount);
    if (!form.date || !form.desc || Number.isNaN(amt)) {
      return alert("Please complete the form with a valid amount.");
    }
    const newTx = { userId, date: form.date, description: form.desc, amount: amt, category: form.category || "Other" };
    try {
      const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/transactions`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify(newTx)
      });
      const data = await res.json();
      if (data.success) {
        setTransactions(prev => [data.transaction, ...prev]);
        setForm({ date: "", desc: "", amount: "", category: "" });
      } else alert(data.message || "Failed to add transaction");
    } catch {
      alert("Error adding transaction");
    }
  }

  async function removeTransaction(id) {
    try {
      const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/transactions/${id}`, { method: "DELETE" });
      const data = await res.json();
      if (data.success) setTransactions(prev => prev.filter(t => t.id !== id));
    } catch {
      alert("Error removing transaction");
    }
  }

  async function updateBudget(newBudget) {
    setBudget(newBudget);
    try {
      await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/budget`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ userId, monthYear, amount: newBudget })
      });
    } catch (err) {
      console.error(err);
    }
  }

  const filtered = transactions.filter(t => {
    if (!filterText) return true;
    const q = filterText.toLowerCase();
    return (`${t.description} ${t.category} ${t.date}`).toLowerCase().includes(q);
  });

  const progress = Math.min(100, Math.round((totals.spent / budget) * 100));
  if (loading) return <div className="loading">Loading...</div>;

  return (
    <div className="dashboard">
      <header className="dashboard-header">
        <div>
          <h1>BudgetTrack</h1>
          <p>Track your spending and control your budget easily.</p>
        </div>
        <div className="budget-box">
          <label>Monthly Budget</label>
          <input type="number" value={budget} onChange={(e) => updateBudget(Number(e.target.value || 0))} />
          <div>{currency(budget)}</div>
        </div>
      </header>

      <div className="dashboard-grid">
        <div className="card">
          <h3>Overview</h3>
          <div className="overview">
            <div><span>Spent</span><strong className="text-red">{currency(totals.spent)}</strong></div>
            <div><span>Income</span><strong className="text-green">{currency(totals.income)}</strong></div>
            <div><span>Remaining</span><strong>{currency(budget - totals.spent)}</strong></div>
          </div>
          <div className="progress-bar">
            <div style={{ width: `${progress}%` }}></div>
          </div>
          <p className="text-muted">{progress}% used</p>
        </div>

        <div className="card">
          <h3>Spending by Category</h3>
          <div className="chart">
            <ResponsiveContainer>
              <PieChart>
                <Pie data={categoryBreakdown} dataKey="value" nameKey="name" outerRadius={80}>
                  {categoryBreakdown.map((_, i) => <Cell key={i} fill={COLORS[i % COLORS.length]} />)}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </div>
          <ul className="category-list">
            {categoryBreakdown.map((c, i) => (
              <li key={i}>
                <span><span className="dot" style={{ background: COLORS[i % COLORS.length] }}></span>{c.name}</span>
                <span>{currency(c.value)}</span>
              </li>
            ))}
          </ul>
        </div>
      </div>

      <div className="dashboard-grid">
        <div className="card">
          <h3>Quick Add</h3>
          <form onSubmit={addTransaction}>
            <input type="date" value={form.date} onChange={(e) => setForm({ ...form, date: e.target.value })} />
            <input type="text" placeholder="Description" value={form.desc} onChange={(e) => setForm({ ...form, desc: e.target.value })} />
            <input type="text" placeholder="Amount (positive=expense, negative=income)" value={form.amount} onChange={(e) => setForm({ ...form, amount: e.target.value })} />
            <input type="text" placeholder="Category" value={form.category} onChange={(e) => setForm({ ...form, category: e.target.value })} />
            <div className="form-actions">
              <button className="btn primary">Add</button>
              <button type="button" className="btn secondary" onClick={() => setForm({ date: "", desc: "", amount: "", category: "" })}>Reset</button>
            </div>
          </form>
        </div>

        <div className="card">
          <div className="transactions-header">
            <h3>Transactions</h3>
            <div className="search-box">
              <input type="text" placeholder="Search..." value={filterText} onChange={(e) => setFilterText(e.target.value)} />
              <button onClick={() => setFilterText("")}></button>
            </div>
          </div>
          <table className="transactions">
            <thead>
              <tr><th>Date</th><th>Description</th><th>Category</th><th>Amount</th><th></th></tr>
            </thead>
            <tbody>
              {filtered.map((t) => (
                <tr key={t.id}>
                  <td>{t.date}</td>
                  <td>{t.description}</td>
                  <td>{t.category}</td>
                  <td className={t.amount > 0 ? "text-red" : "text-green"}>{currency(t.amount)}</td>
                  <td><button className="btn danger small" onClick={() => removeTransaction(t.id)}>Remove</button></td>
                </tr>
              ))}
            </tbody>
          </table>
          <div className="table-footer">
            <span>Showing {filtered.length} transactions</span>
            <span>Total: {currency(filtered.reduce((s, t) => s + t.amount, 0))}</span>
          </div>
        </div>
      </div>
      <div className="container-dashboard"></div>
    </div>
  );
}
export default Dashboard;
