import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "../Styles/AdminChoice.css";

const  AdminChoice =() => {

  const [data, setData] = useState(null);
  const [user, setUser] = useState(null);
  const navigate = useNavigate();
  useEffect(() => {
    const fetchUser = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/auth/me`, {
          withCredentials: true,
        });
        if (res.data !=null){
          setData(res.data);
        }
        if (res.data.user.full_name !== "Ade1") {
          alert("Access denied  Admins only");
          navigate("/");
        } else {
          setUser(res.data.user);
        }
      } catch {
        navigate("/login");
      }
    };
    fetchUser();
  }, [navigate]);


  if (!data) return <div className="adminx-loading">Loading Admin Panel...</div>;

  return (
    
    <div className="admin-Container">
      <h1>Admin Panel</h1>
      <div className="row">
      <button className="adminbtn1" onClick={() => navigate("/admin/hero")}>Edit Hero</button>
      <button className="adminbtn2" onClick={() => navigate("/admin/about")}>Edit About</button>
      <button className="adminbtn3" onClick={() => navigate("/admin/contact")}>Edit Contact-US</button>
      <button className="adminbtn4" onClick={() => navigate("/admin/users")}>Edit Users</button>
      <button className="adminbtn5" onClick={() => navigate("/admin/support")}>Support Messages</button>
      <button className="adminbtn1" onClick={() => navigate("/admin/carousel")}>Edit Carousel</button>
      <button className="adminbtn2" onClick={() => navigate("/admin/stats")}>Edit Stats</button>
      <button className="adminbtn3" onClick={() => navigate("/admin/features")}>Edit Features</button>
      </div>
      <div className="admin-decoration"></div>

    </div>
  );
}

export default AdminChoice;